import * as S from "../../style";
import H2 from "../../../../components/H2";
import Circle from "../../../../assets/svgComponents/Circle";

interface AboutMeContract {
    isMobile: boolean;
}

const AboutMeSection = ({ isMobile }: AboutMeContract) => {
    return (
        <S.Section id="aboutMe">
            <div className="centralizer">
                <H2>Sobre mim</H2>
                <S.AboutMeContainer>
                    <div>
                        <p>
                        Ol, sou Hugo Furtado, um entusiasta de tecnologia que decidiu seguir o caminho da 
                        engenharia informtica para aprimorar minhas habilidades em programao. Ao longo da minha jornada, estive 
                        envolvido em projetos diversos, particularmente focados em criar sites, administrao de redes e 
                        administrao de bancos de dados. Essa experincia permitiu-me explorar as complexidades do desenvolvimento 
                        de software, visando a eficcia operacional. Estou comprometido em continuar aprendendo e aplicando 
                        meus conhecimentos 
                        para contribuir com solues inovadoras e eficazes no campo da tecnologia. O que mais gosto de fazer nos 
                        meus tempos livres  jogar bola ou assistir a jogos de futebol. Adoro a emoo e a energia que o esporte 
                        proporciona. Alm disso, a prtica regular me ajuda a relaxar, me mantm ativo e me permite socializar com 
                        amigos que compartilham do mesmo interesse.
                        </p>
                        <br />
                        <p>
                            Atualmente estou a terminar o curso de engenharia informtica.
                        </p>
                    </div>
                    {!isMobile && (
                        <div className="svgContainer">
                            <Circle />
                        </div>
                    )}
                </S.AboutMeContainer>
            </div>
        </S.Section>
    );
};

export default AboutMeSection;
